html {
    font-family: 'Eurostile', sans-serif;
    background: #ffffff; /* Ajoutez vos couleurs préférées ici */
    color: #004343;
    height:100vh;
    margin:0;
}

* {
  box-sizing: border-box;
}

body{
  margin: 0;
}

header {
  font-size: 18px;
  padding-top: 1.2%;
  position: fixed;
  width: 100%;
  height:fit-content;
  display: flex;
  z-index: 1000;
}

.logo{
  display: inline; /* Ajustez la largeur selon vos besoins */
  height: 61px; /* Permet de conserver les proportions de l'image */
  width: 135px;
  margin-left: 4%;
}

ul {
  white-space: nowrap;
  width: 100%;
  list-style: none;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0;
  padding: 0;
}

.header-menu li {
  margin-right: 2%;

}

.button_openPopUp {
  background-color: rgb(56, 56, 56, 0.9);
  color: white;
  padding: 15px 20px; /* Adjust the values as needed */
  border-radius: 7px;

}

a {
  text-decoration: none;
  /* color: white; */
}

footer {
  text-align: center;
  padding: 15px 0;
  width: 100%;
  position: fixed; /* Modifier à 'fixed' */
  bottom: 0;
}

section {
  padding-top: 5%;
  height: 100vh;
}

/* Section 1 */

#section1 {
  margin: 0;
  padding: 0;
  height: 110vh; /* Réglez la hauteur sur 100% de la hauteur de la vue (viewport) */
  background-image: url('./background_1.jpg');
  background-size: cover; /* Ajuste la hauteur pour couvrir, mais maintient la largeur d'origine */
  background-position: center;
  background-repeat: no-repeat;
}

.title_section1{
  width:50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: justify;
  line-height: 1.5; /* Ajustez la valeur selon vos préférences */
  margin-left: 4%;
}

.section1 h1 {
  /* font-family: 'Didot', sans-serif; */
  font-family: 'Abril Fatface';
  font-weight: bold;
  text-align: left;
  font-size: 60px;
  color: rgb(56, 56, 56);
  margin-top: 8%; /* Resetting the default top margin */
  margin-right: 0; /* Resetting the default right margin */
  margin-bottom: 2%; /* Resetting the default bottom margin */
  margin-left: 5%; /* Resetting the default left margin */
}

.section1 h2 {
  /* font-family: 'Didot', sans-serif; */
  font-family: 'Abril Fatface';
  font-weight: bold;
  text-align: left;
  font-size: 35px;
  color: rgb(56, 56, 56);
  width: 60%;
  margin-top: 5%; /* Resetting the default top margin */
  margin-right: 0; /* Resetting the default right margin */
  margin-bottom: 2%; /* Resetting the default bottom margin */
  margin-left: 5%; /* Resetting the default left margin */
}

.section1 p {
  margin:0;
  font-size: 22px;
  color: rgb(56, 56, 56);
  margin-left: 3%; /* Resetting the default left margin */
  width: 107%;
} 

.phone {
  position: absolute;
  top: 18%;
  right: 10%;
  width: 19%;
}


/* Section 2 */

#section2 {
  margin: 0;
