}

.S4_email {
  margin-top: 1%;
  color: rgb(56, 56, 56);
  border-bottom: 1px solid rgb(56, 56, 56);
  font-family: 'Eurostile';
  font-size: 25px;
  font-weight:bold;
  width: 80%;
  margin-left: 3%; /* Resetting the default left margin */
}

.S4_button_signUp {
  text-decoration: none;
  background-color: rgb(56, 56, 56, 0.9);
  font-size: 20px;
  border: none; /* Remove border */
  cursor: pointer; /* Add a pointer cursor on hover (optional) */
  color: white;
  padding: 15px 20px; /* Adjust the values as needed */
  margin-top: 3%;
  border-radius: 7px;
  font-family: 'Eurostile';
  width: 35%;
  margin-left: 3%; /* Resetting the default left margin */
}

.email-message {
  color: rgb(0, 67, 67); /* Changer la couleur du texte en bleu */
  font-weight:bold;

}
.email-error {
  color: rgb(209, 27, 27); /* Définir la couleur du texte en rouge */
}

.S4_2 {
  margin-top: 1%;
  margin-left: 2%; /* Resetting the default left margin */
  display: flex;
  flex-direction: row; /* Aligne les éléments en ligne */
  justify-content: space-between; /* Espace équitablement les éléments */
  width: 100%;
}

.S4_3 {
  width: 50%; /* Ajustez la largeur pour contrôler l'espace entre les éléments */
}

.S4_4 {
  background-color: rgb(192, 199, 199, 0.4);
  height: 85%;
  width: 95%;
  border-radius: 7px;

}

.social-icons img {
  max-width: 20%; /* Spécifie une largeur maximale de 50 pixels */
  height: auto; /* Garde le ratio hauteur/largeur de l'image */
  margin-right: 3%;
}

.social-icons {
  margin-top: 8%; /* Supprime l'espacement entre les icônes */
  width: 100%; /* Spécifie une largeur de 50 pixels */
  height: 50px; /* Spécifie une hauteur de 50 pixels */
  justify-content: space-between;
}




