/* AuthPopup.css */

.auth-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  position: relative;
  border: none;
  margin: 0;
  padding: 0;
  border-radius: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  display: flex;
  max-width: 1500px;
  width: 60%;
  background-color: rgb(0, 67, 67);;
  height: 60%;
  border-radius: 7px;


}

.form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
  padding: 20px;
}

.form-title{
  font-family: 'Abril Fatface';
  padding-bottom: 5px;
  color:white;
  font-size: 26px;  
}

.form-item {
  width: 90%;
  color:white;
}
.form-item input::placeholder {
  color: white;
}

label {
  margin-bottom: 8px;
}

.input-container {
  width: 100%;
}

input {
  border: none;
  border-bottom: 1px solid white;
  width: 100%;
  padding: 8px;
  margin-bottom: 16px;
  outline: none;
  background:none;
  color : white;
  font-family: 'Eurostile';
  font-size: 20px;
}

.second-container {
  width: 90%; /* Ajustez la largeur selon vos besoins */
  overflow: hidden;
  /* Ajoutez d'autres styles au besoin pour le deuxième conteneur */
  div{
    margin-bottom: 20px;
  }
}

.close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
  font-size: 18px;
  color : white;
}

.button-container {
  padding-top: 0%;
  border:None;
  align-items: center; /* Center items vertically */
  justify-content: center; /* Center items horizontally */
}

.button_signUp {
  text-decoration: none;
  color: rgb(56, 56, 56);
  font-size: 20px;
  background: transparent; /* Set background to transparent */
  border: none; /* Remove border */
  cursor: pointer; /* Add a pointer cursor on hover (optional) */
  background-color: white;
  padding: 15px 20px; /* Adjust the values as needed */
  margin-bottom: 5%;
  font-weight: bold;
  border-radius: 7px;
  font-family: 'Eurostile';
}

.email-message2 {
  color: white; /* Changer la couleur du texte en bleu */
}
.email-error2 {
  color: rgb(209, 27, 27); /* Définir la couleur du texte en rouge */
}

.social-icons img {
  max-width: 16%; /* Spécifie une largeur maximale de 50 pixels */
  height: auto; /* Garde le ratio hauteur/largeur de l'image */
}

.social-icons {
  width: 100%; /* Spécifie une largeur de 50 pixels */
  height: 60px; /* Spécifie une hauteur de 50 pixels */
  justify-content: space-between;
  margin-top: 0; /* Supprimez la marge en haut de la liste d'icônes */
}






.image-slider {
  width: 42%; /* Largeur fixe du conteneur */
  display: flex; /* Utiliser flexbox pour centrer les éléments */
  justify-content: center; /* Centrer les éléments horizontalement */
  background-color: rgb(192, 199, 199);
  border-radius: 0 7px 7px 0; /* Appliquer une bordure arrondie seulement sur les bords droits */
}

.image_element{
  display: flex;
  justify-content: center;
  margin: 16%
}

.caroussel {
  max-width: 270px;
  margin: 0 auto;
  width : 100%;
}

.image{
  height: auto;
  width: auto;
  overflow: hidden;
  border-radius: 7px;
}






